.profile {
  max-width: 100%;
  margin: 0 auto;
}

.description {
  margin: 0 auto;
  text-align: center;
  width: 25%;
  background-color: #e2dada;
  border: 1px solid black;
}

.description p:last-child {
  margin-bottom: 0;
}

.avatar {

  width: 150px;
  margin: 15px auto;
  border-radius: 50%;
  border: 2px solid yellowgreen;
}

.name {
  font-size: 16px;
  font-weight: bold;
}

.tag {
  font-size: 14px;
  color: grey;
}

.location {
  font-size: 14px;
  color: grey;
}

.stats {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  width: 25%;
  margin: 0 auto;
}

.stats li {
  width: 33.33%;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  gap: 5px;
  text-align: center;
  border: 1px solid black;
  background: rgb(233, 230, 230);
}

.label {
  font-size: 14px;
  color: grey;
}

.quantity {
  font-size: 16px;
  font-weight: bold;
}